import { Alert, AlertIcon, Box, Divider, Flex, Heading, Input, SlideFade, Stack, Text, Link as ChakraLink } from "@chakra-ui/react";
import { ChangeEvent, FC, FormEvent, memo, useEffect, useState } from "react";
import { createUserWithEmailAndPassword, sendEmailVerification, sendSignInLinkToEmail } from "firebase/auth";
import { useNavigate, Link as RouterLink } from "react-router-dom";
import { PrimaryButton } from "../parts/PrimaryButton";
import { auth } from "../../firebase";
import { FormFrame } from '../parts/FormFrame';
import { LoadingPage } from '../parts/LoadingPage';
import { useRecoilValue } from 'recoil';
import { loadingState, userState } from '../../recoil/AuthState';

export const SignUp: FC = memo(() => {
  const [show, setShow] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const onChangeUserId = (e: ChangeEvent<HTMLInputElement>) => setEmail(e.target.value);
  const onChangePassword = (e: ChangeEvent<HTMLInputElement>) => setPassword(e.target.value);
  const navigation = useNavigate();
  const user = useRecoilValue(userState);
  const loading = useRecoilValue(loadingState)

  useEffect(() => {
    if (user && user.emailVerified) {
      navigation("/dashboard/");
    }
  }, [user, navigation]);

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const form = event.target as HTMLFormElement;
    const emailElement = form.elements.namedItem("email") as HTMLInputElement;
    const passwordElement = form.elements.namedItem("password") as HTMLInputElement;
    setShow(false);
    if (emailElement && passwordElement) {
      createUserWithEmailAndPassword(auth, emailElement.value, passwordElement.value)
        .then(userCredential => {
          const user = userCredential.user;
          if (user) {
            sendEmailVerification(user)
              .then(() => {
                console.log("Verification email sent!");
              })
              .catch(error => {
                console.error("Error sending verification email:", error.message);
              });
          }
          navigation("/auth/thanks");
        })
        .catch((error: { code: string }) => {
          let message: string;
          switch (error.code) {
            case "auth/email-already-in-use":
              message = "";
              break;
            case "auth/invalid-email":
              message = "";
              break;
            default:
              message = "";
          }
          setErrorMessage(message);
          setShow(true);
        });
    }
  };

  return (
    <>
      {loading ? (
        <LoadingPage />
      ) : (
      <FormFrame>
        <SlideFade in={show} offsetY="20px">
          <Alert
            status="error"
            textAlign="center"
            position="absolute"
            top="0"
            left="0"
            transform="translateY(-120%)"
            borderRadius="md"
          >
            <AlertIcon />
            {errorMessage}
          </Alert>
        </SlideFade>
        <Heading as="h1" size="lg" textAlign="center">
          
        </Heading>
        <Divider my={4} />
        <Box onSubmit={handleSubmit} as="form" px={10} pb={8}>
          <Stack spacing={1} pt={4}>
            <Text>Email</Text>
            <Input
              name="email"
              autoComplete="email"
              placeholder="info@email.com"
              value={email}
              onChange={onChangeUserId}
            />
          </Stack>
          <Stack spacing={1} py={4} mb={3}>
            <Text></Text>
            <Input name="password" placeholder="" value={password} onChange={onChangePassword} />
            <Text fontSize="sm" color="gray">
              6
            </Text>
          </Stack>
          <Stack>
            <PrimaryButton disabled={email === ""} onClick={() => null}>
              
            </PrimaryButton>
          </Stack>
          <Flex justifyContent="flex-end" mt={2}>
              <ChakraLink as={RouterLink} to="/auth/" color="black.500" fontSize="sm">
                
              </ChakraLink>
            </Flex>
        </Box>
      </FormFrame>
      )}
    </>
  );
});
